.article-single{padding-top:75px}.article-single__date{font-size:12px;line-height:16px;margin-bottom:20px}.article-single__title{max-width:916px;font-size:34px;line-height:40px;font-weight:800;margin-top:30px;margin-bottom:30px}.article-single__content{max-width:812px}.article-single__paragraph{font-size:18px;line-height:28px;margin-top:30px;margin-bottom:30px}.article-single__paragraph p a{font-weight:700;user-select:text;transition:all .3s ease;text-decoration-color:currentColor;cursor:pointer;color:#252525}.article-single__paragraph p a:hover{text-decoration-color:#fff}.article-single__heading{font-size:29px;line-height:30px;font-weight:800;margin-top:50px;margin-bottom:30px}.article-single__img{position:relative}.article-single__img img{width:100%}.article-single__img-copyright{font-size:12px;line-height:16px;margin-top:16px}.article-single__img-caption{position:absolute;top:0;right:-416px;text-align:right;width:376px;margin-top:0;border-top:2px solid #252525;padding-top:12px;font-style:italic;font-size:14px;line-height:19px}.article-single__slider-item{max-width:812px}.article-single__tags{display:flex;flex-wrap:wrap;align-items:center;margin-top:50px;margin-bottom:50px}.article-single__tags-item{cursor:pointer;margin-bottom:10px;display:block;font-size:13px;line-height:40px;color:#252525;color:var(--colorBlack);text-decoration:none;background:#efefef;padding:0 18px;white-space:nowrap;border-radius:8px;margin-right:10px}.article-single__quote{margin:50px 0;background:#efefef;padding:70px 30px 30px 40px;box-sizing:border-box;position:relative}.article-single__quote::before{content:"";width:30px;height:30px;position:absolute;top:33px;left:43px;background:url(quote.svg) no-repeat 0 0;background-size:100% auto}.article-single__quote-text{width:520px;max-width:100%;font-style:italic;font-weight:600;font-size:19px;line-height:26px}.article-single__quote-author{display:block;width:520px;max-width:100%;font-style:italic;font-size:14px;line-height:19px;margin-top:30px}.article-single__quote-author::before{content:"— "}